
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.container {
  max-width: 600px;
  margin: 0 auto;
}
.logo {
  display: block;
  margin: 20px auto;
  height: 75px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  margin-top: 60px;
}

.textinput {
  width: 100%;
  padding: 8px 18px;
  font-size: 16px;
  margin-bottom: 16px;
}
.textedit {
  width: 85%;
  padding: 8px 18px;
  font-size: 16px;
  margin-bottom: 16px;
}
.add {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-bottom: 3%;
}
.remove {
  cursor: pointer;
  text-align: center;
  font-size: 20px;
}
.edit {
  cursor: pointer;
  text-align: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding-bottom: 1%;
}
.addButton {
  background-color: aquamarine;
  border: aquamarine;
  padding: 2%;
}
.completeButton {
  background-color: greenyellow;
  border: greenyellow;
  padding: 1%;
}
.completeLabel {
  background-color: green;
  border: green;
  padding: 1%;
  font-size: 12px;
  width: 15%;
  text-align: center;
  font-weight: bold;
  color: white;
  border-radius: 5px;
  margin-bottom: 12px;
}
